import getPositionOfCell from '../../../utils/utils/getPositionOfCell'
import { createEmptyTableCell } from '../../../bi/utils';
import { createTableInstance } from '../helpers';

const table = createTableInstance();

describe('bi/table/utils/getPositionOfCell', () => {
  it('cell is child of table', () => {
    const colIndex = 1;
    const rowIndex = 1;
    const cell = table.nodes[rowIndex].nodes[colIndex];
    expect(getPositionOfCell(cell.key, table)).toEqual({
      colIndex,
      rowIndex,
    });
  });

  it('cell is not child of table', () => {
    const cell = createEmptyTableCell();
    expect(getPositionOfCell(cell.key, table)).toBe(null);
  });
});
